.App {
  text-align: center;
}
.responsive {
  width: 100%;
  height: auto;
} 
.container {
  position: relative;
  width: 100%;
  height: 820px;
}
.background-image {
  background-image: url("/public/bg-dark2.jpg"); 
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}


h1{
  margin-top:50px;
  font-size:30px;
  color:white;
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-shadow: rgb(255, 255, 255) 1px 0 1px;
}
.converter .innerDiv h2{
  color:#3474e0;
  font-size: 25px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-shadow: rgb(16, 50, 204) 1px 0px 15px;
}
.span-1{
  font-size: 30px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  text-shadow: rgb(16, 50, 204) 0px 7px 15px;
}
.converter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.7);
  padding: 34px 20px;
  color: white;
  border-radius:20px;
  border:5px solid black;
}

.grid-container {
  display: grid;
  height: 200px;
  align-content: center;
  grid-template-columns: auto auto;
  gap: 12px;
  background-color: rgb(0, 0, 0);
  padding:5px;
  margin:10px 0;
  border-radius:10px;
}

.grid-container > div {
  background-color: rgb(0, 0, 0);
  text-align: center;
  padding: 15px 0;
  height:155px;
  font-family:Georgia, 'Times New Roman', Times, serif;;
  font-size: 28px;
  color:#6cbfff;
  text-shadow: rgb(0, 4, 255) 1px 0 10px;
  
}
.remarks{
  pointer-events: none;
  font-size: 29px;
  color:rgb(249, 189, 10);
  outline:none;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
  font-family: math;
}
.gpaOutput{
  pointer-events: none;
  text-align: center;
  font-size: 40px;
  margin-top: 15px;
  height:40px;
  margin:0px 10px;
  width:200px;
  color:white;
  text-shadow: rgb(254, 254, 254) 1px 0 10px;
  outline:none;
  border:2px solid rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0);
  font-family: math;
}
.gradeOutput{
  pointer-events: none;
  text-align: center;
  font-size: 40px;
  margin:0px 10px;
  width:200px;
  height:50px;
  color:rgb(6, 230, 29);
  outline:none;
  border:2px solid rgba(0, 0, 0, 0);
  background-color: rgba(0, 0, 0, 0);
  font-family: math;
}
.tempoutput{
  pointer-events: none;
  text-align: center;
  font-size: 29px;
  padding:0 15px;
  margin:0px 10px;
  width:100px;
  height:100px;
  color:white;
  text-shadow: rgb(254, 254, 254) 1px 0 10px;
  outline:none;
  border:2px solid rgb(0, 0, 0);
  background-color: rgb(0, 0, 0);
  font-family: math;
}
select{
  border:2px solid grey;
  outline:none;
}
.selectcredit {
  padding: 10px;
  width: 80px;
  margin: 5px;
  border: 2px solid rgba(255, 255, 255, 0.5); /* Frosty border */
  border-radius: 10px;
  background: rgba(255, 255, 255, 0); /* Frosted glass effect */
  backdrop-filter: blur(10px); /* Glassmorphism effect */
  font-size: 16px;
  font-weight: bold;
  color: #ffffff;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Hover effect - gives it a slight glow */
.selectcredit:hover {
  border-color: rgba(200, 220, 255, 0.8);
  background: rgba(255, 255, 255, 0.064);
  box-shadow: 0 0 10px rgba(200, 220, 255, 0.5);
}

/* Focus Effect - icy glow */
.selectcredit:focus {
  border-color: #0399ef; /* Light blue */
  box-shadow: 0 0 10px rgba(128, 208, 255, 0.7);
}

/* Remove default dropdown arrow on some browsers */
.selectcredit::-ms-expand {
  display: none;
}

/* Style for dropdown options */
.selectcredit option {
  background: rgba(0, 0, 50, 0.8);
  color: white;
}


.selectinput{
  background-color: rgb(249, 249, 249);
  border:2px solid rgb(0, 0, 0);
  border-radius:8px;
  outline:none;
  padding:9px;
  margin:5px 10px;
  width:180px;
  font:bold;
  font-size:15px;
}


.convert-Btn{
  padding:10px;
  background-color: #2281c9;
  color:white;
  outline:none;
  border:2px solid black;
  border-radius:10px;
  
}

select{
  font-size: 15px;
  font:bold;
  background-color: rgb(255, 255, 255);
}



@media only screen and (min-width: 600px) {
  .copyright{
    display: none;
  }
}
@media only screen and (max-width: 600px) {
  .background-image {
    background-image: url("/public/bg-dark4.jpg"); 
    background-size: cover;
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .selectinput{
    width:150px;
  }
  .copyright{
    font-size:12px;
    color:white;
    font-family:revert;
    margin-top:618px;
  }
  .convert-Btn{
    padding:10px;
    background-color: #2281c9;
    margin-top: 10px;
    color:white;
    outline:none;
    border:2px solid black;
    border-radius:10px;
    
  }
  h1{
    margin-top:50px;
    font-size:24px;
    color:white;
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-shadow: rgb(255, 255, 255) 1px 0 1px;
  }
  .gpaOutput{
    font-size: 35px;
    margin-top: 12px;
    height:40px;
  }
  .gradeOutput{
    height:50px;
    font-size: 30px;
  }
  .remarks{
    font-size: 20px;
  }
  .innerDiv{
    padding: auto;
  }
  .converter {
    padding: 34px 45px;
  }
  .converter .innerDiv h2{
    font-size: 18px;
  }
  .selectcredit{
    width: 70px;
  }
}







.popper {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  opacity: 0;
}

